			//本地模拟订单提交UI效果
			uni.showLoading({
				title:'正在获取订单...'
			})
			
			let paymentOrder = [];
			paymentOrder.push(row);
			setTimeout(()=>{
				uni.setStorage({
					key:'paymentOrder',
					data:paymentOrder,
					success: () => {
						uni.hideLoading();
						uni.navigateTo({
							url:'../../pay/payment/payment?amount='+row.payment
						})
					}
				})
			},500)
		},
